@font-face {
  font-family: 'IranSans';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/iran_sans/iransans-regular.woff);
}

@font-face {
  font-family: 'IranSans';
  font-style: normal;
  font-weight: 500;
  src: url(../fonts/iran_sans/iransans-medium.woff);
}

@font-face {
  font-family: 'IranSans';
  font-style: normal;
  font-weight: 700;
  src: url(../fonts/iran_sans/iransans-bold.woff);
}

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/material_icons/MaterialIcons-Regular.eot); /* For IE6-8 */
  src: local('Material Icons'),
  local('MaterialIcons-Regular'),
  url(../fonts/material_icons/MaterialIcons-Regular.woff2) format('woff2'),
  url(../fonts/material_icons/MaterialIcons-Regular.woff) format('woff'),
  url(../fonts/material_icons/MaterialIcons-Regular.ttf) format('truetype');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px; /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;

  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}
